  animation: fill .4s ease-in-out .4s forwards, scale .3s ease-in-out .9s both;
  border-radius: 50%;
  -webkit-box-shadow: inset 0 0 0 #5bc142;
  box-shadow: inset 0 0 0 #5bc142;
  stroke-width: 2;
  stroke: #fff;
  stroke-miterlimit: 10
}

.qube-tools-last .checkmark-circle {
  -webkit-animation: stroke .6s cubic-bezier(.65, 0, .45, 1) forwards;
  animation: stroke .6s cubic-bezier(.65, 0, .45, 1) forwards;
  stroke-dasharray: 166;
  stroke-dashoffset: 166;
  stroke-width: 2;
  stroke-miterlimit: 10;
  stroke: #5bc142;
  fill: none
}

.qube-tools-last .checkmark-check {
  -webkit-transform-origin: 50% 50%;
  -ms-transform-origin: 50% 50%;
  transform-origin: 50% 50%;
  -webkit-animation: stroke .3s cubic-bezier(.65, 0, .45, 1) .8s forwards;
  animation: stroke .3s cubic-bezier(.65, 0, .45, 1) .8s forwards;
  stroke-dasharray: 48;
  stroke-dashoffset: 48
}

@-webkit-keyframes stroke {
  100% {
    stroke-dashoffset: 0
  }
}

@keyframes stroke {
  100% {
    stroke-dashoffset: 0
  }
}

@-webkit-keyframes scale {
  0%,
  100% {
    -webkit-transform: none;
    transform: none
  }
  50% {
    -webkit-transform: scale3d(1.1, 1.1, 1);
    transform: scale3d(1.1, 1.1, 1)
  }
}

@keyframes scale {
  0%,
  100% {
    -webkit-transform: none;
    transform: none
  }
  50% {
    -webkit-transform: scale3d(1.1, 1.1, 1);
    transform: scale3d(1.1, 1.1, 1)
  }
}

@-webkit-keyframes fill {
  100% {
    -webkit-box-shadow: inset 0 0 0 100px #5bc142;
    box-shadow: inset 0 0 0 100px #5bc142
  }
}

@keyframes fill {
